import React from 'react';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import Layout from '../components/layouts'
import RepositoryList, { REPOSITORY_FRAGMENT } from '../Repository';
import Loading from '../components/Loading';
import ErrorMessage from '../components/Error';

const GET_REPOSITORIES_OF_CURRENT_USER = gql`
  {
    user(login: "ehabdevel") {
      id
      repositories(last: 5, isFork: false) {
        edges {
          node {
            ...repository
          }
        }
      }
    }
  }
  ${REPOSITORY_FRAGMENT}
`;

const Githubrepos = () => (
	<Layout>
	  <Query query={GET_REPOSITORIES_OF_CURRENT_USER}>
	    {({ data, loading, error }) => {
	      if (error) {
	        return <ErrorMessage error={error} />;
	      }

	      const { user } = data;

	      if (loading || !user) {
	        return <Loading />;
	      }

	      return (
            <div className="col-sm-12">
              <div className="col-sm-12 text-center p-3 mb-3" style={{backgroundColor: '#eee',}}>
                <p className="h2">Created Repositories with Github</p>
              </div>
              <RepositoryList repositories={user.repositories} />;
            </div>
        )

	    }}
	  </Query>
	</Layout>
);

export default Githubrepos;